.wrapper {
  display: flex;
  position: relative;
}

.currentOption {
  min-width: 80px;
}

.currentOptions_text {
  user-select: none;
  color: #3f4e65;
  cursor: pointer;
  margin-right: 5px;
}

.options__wrapper {
  position: absolute;
  top: 20px;
  left: 0;
  display: flex;
  flex-direction: column;
  background-color: inherit;
  background: white;
  white-space: nowrap;
}

.option {
  user-select: none;
  cursor: pointer;
}

.option > img {
  margin-left: 6px;
}

.reverse {
  transform: rotate(180deg);
}

.hidden {
  display: none;
}
